.image-zoom:hover {
	transition: -webkit-transform .1s;
	transition: transform .1s;
	transition: transform .1s, -webkit-transform .1s; /* IE 9 */
	-webkit-transform: scale(3) translate(30px); /* Safari 3-8 */
	transform: scale(3) translate(100px);
}


